import { ChainId, Token, WETH } from '@uniswap/sdk'

type AllTokens = Readonly<{ [chainId in ChainId]: Readonly<{ [tokenAddress: string]: Token }> }>

export const ALL_TOKENS: AllTokens = [
  ...Object.values(WETH),
]
  // remap WETH to ETH
  // .map(token => {
  //   if (token.equals(WETH[(token as Token).chainId])) {
  //     // ; (token as any).symbol = 'ETH'
  //     //   ; (token as any).name = 'Ether'
  //   }
  //   return token
  // })
  // put into an object
  .reduce<AllTokens>(
    (tokenMap: any, token: any) => {
      if (tokenMap[token.chainId][token.address] !== undefined) throw Error('Duplicate tokens.')
      return {
        ...tokenMap,
        [token.chainId]: {
          ...tokenMap[token.chainId],
          [token.address]: token
        }
      }
    },
    {
      [ChainId.BINANCE]: {},
      [ChainId.ROPSTEN]: {},
      [ChainId.AVAX]: {},
      [ChainId.FANTOM]: {},
      [ChainId.MUMBAI]: {}
    }
  )
